<template>
  <div id="iframe_cont" class="bg-dark p-0" style="">
    <textarea readonly id="textarea" style="transform: 'translateY(-300vh)'"></textarea>
    <iframe
      id="video"
      :style="played ? 'opacity:1' : 'opacity:0'"
      :src="
        src +
        '?theme=dark&autoplay=1&autohide=0&cc_load_policy=1&modestbranding=1&fs=0&showinfo=0&rel=0&iv_load_policy=3&mute=0&loop=0&enablejsapi=1&version=3&playerapiid=ytplayer&controls=1'
      "
      frameborder="3"
      allow="autoplay;"
      allownetworking="internal"
      sandbox="allow-forms allow-scripts allow-pointer-lock allow-same-origin allow-top-navigation"
    >
    </iframe>
    <div class="cover-top"></div>
    <!------------------------------------->
    <div @click="$refs.ctrl.classList.toggle('show')" class="toggleCtrl">
      <i class="fa fa-cogs"></i>
    </div>
    <!------------------------------------->

    <div ref="ctrl" class="ctrl_bar">
      <div v-if="src != ''" class="ctrl_btns bg-dark">
        <div class="ctrl_btn">
          <button @click="toggleVideoPlay">
            <i class="ctrl_btn" :class="playing ? 'fa fa-pause' : 'fa fa-play'"></i>
          </button>
        </div>
        <div class="ctrl_btn">
          <div class="slidecontainer">
            <input
              type="range"
              :step="0.25"
              :min="0.25"
              :max="2"
              @change="setSpeed"
              v-model="rate"
              class="slider"
              id="myRange"
            />
          </div>
          <label>speed</label>
        </div>
        <div class="ctrl_btn">
          <div class="slidecontainer">
            <input
              type="range"
              :step="1"
              :min="0"
              :max="100"
              @change="setVolume"
              v-model="volume"
              class="slider"
              id="myRange"
            />
          </div>
          <label>volume</label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  Capacitor,
  Plugins,
  Permissions,
  PermissionsOptions,
  PermissionType,
} from "@capacitor/core";
import Axios from "axios";

export default {
  props: ["src", "userData", "lessonId"],
  mounted() {
    this.mobile = Capacitor.isNative;
    this.deleteClipboard();
    setTimeout(() => {
      const vid = document.getElementById("video");
      vid.contentWindow.postMessage(
        '{"event":"command","func":"' + "playVideo" + '","args":""}',
        "*"
      );
      this.playing = true;
      this.played = true;
    }, 1500);
    //////////////////////////////
    this.setTimeOut2 = setTimeout(() => {
      Axios.post("/addVideoView", {
        userId: this.$store.getters.userId,
        lessonId: this.lessonId,
      }).then((res) => {});
    }, 120000);
  },
  async destroyed() {
    // await this.copyToClipboard("كان غيرك أشطر");
    clearTimeout(this.setTimeOut);
    clearTimeout(this.setTimeOut2);
  },
  data() {
    return {
      playing: false,
      played: false,
      mobile: false,
      setTimeOut: null,
      setTimeOut2: null,
      rate: 1,
      volume: 50,
      player: null,
    };
  },
  computed: {
    watermark() {
      return this.$store.getters.adminData.watermark;
    },
  },
  methods: {
    toggleVideoPlay() {
      const vid = document.getElementById("video");
      if (this.playing) {
        vid.contentWindow.postMessage(
          '{"event":"command","func":"' + "pauseVideo" + '","args":""}',
          "*"
        );
        this.playing = false;
      } else {
        vid.contentWindow.postMessage(
          '{"event":"command","func":"' + "playVideo" + '","args":""}',
          "*"
        );
        this.playing = true;
      }
    },
    setSpeed() {
      const rate = +this.rate;
      const vid = document.getElementById("video");
      const comm = {
        event: "command",
        func: "setPlaybackRate",
        args: [rate, true],
      };
      vid.contentWindow.postMessage(JSON.stringify(comm), "*");
    },
    setVolume() {
      const vol = +this.volume;
      const vid = document.getElementById("video");
      const comm = {
        event: "command",
        func: "setVolume",
        args: [vol, true],
      };
      vid.contentWindow.postMessage(JSON.stringify(comm), "*");
    },
    seekTo() {
      const vid = document.getElementById("video");
      const comm = {
        event: "command",
        func: "seekTo",
        args: [200, true],
      };
      vid.contentWindow.postMessage(JSON.stringify(comm), "*");
    },
    fullscreen() {
      const video_con = document.getElementsByClassName("video")[0];
      video_con.classList.toggle("fullscreen");
    },
    deleteClipboard() {
      this.setTimeOut = setTimeout(async () => {
        await this.copyToClipboard("كان غيرك اشطر")
          .then((res) => {
            console.log(res);
          })
          .catch((e) => {
            console.log(e);
          });
        this.deleteClipboard();
      }, 1000);
    },
    copyToClipboard(textToCopy) {
      console.log(window.isSecureContext);
      // alert("Dfff");
      if (this.mobile) return;
      else if (navigator.clipboard != undefined) {
        return new Promise(async (res, rej) => {
          try {
            await navigator.clipboard.writeText(textToCopy);
            res("copied");
          } catch (e) {
            rej(e);
          }
        });
      } else if (window.clipboardData) {
        return new Promise((res, rej) => {
          window.clipboardData.setData("Text", textToCopy);
          res("copied2");
        });
      } else {
        const el = document.getElementById("textarea");
        el.value = textToCopy;
        el.select();
        // document.body.removeChild(el);
        return new Promise((res, rej) => {
          document.execCommand("copy") ? res("copied3") : rej("error");
        });
      }
    },
  },
};
</script>

