<template>
  <div>
    <div class="lesson-content" v-if="lesson">
      <!-------------------LESSON--------------------------->
      <div>
        <div class="vid-info directed">
          <h3>
            {{ lesson.section | section(lesson.stage) }}
          </h3>
          <h3>درس رقم {{ lesson.number }}</h3>
          <h3>{{ lesson.name }}</h3>
          <h3>{{ lesson.stage | stage }}</h3>
        </div>
        <hr />

        <div @click="startVideo" class="start-video">
          <h5>تشغيل الفيديو</h5>
        </div>

        <div v-if="videoStarted" class="video fullscreen">
          <div @click="() => (videoStarted = false)" class="toggle">
            <i class="fa fa-times"></i>
          </div>
          <div class="bg-dark" style="width: 100%; height: 100%">
            <video-iframe
              v-if="videoType == 'youtube-embed' && videoStarted"
              :src="lesson.videoPath"
              :lessonId="lesson._id"
            />
            <video-stream
              v-if="videoType == 'storage-stream' && videoStarted"
              :src="lesson.videoPath"
              :lessonId="lesson._id"
            />
            <youtube-stream
              v-if="videoType == 'youtube-stream' && videoStarted"
              :src="lesson.videoPath"
              :lessonId="lesson._id"
              :password="vidPassword"
            />
          </div>
          <div v-if="watermark" class="watermark aligned">
            <h4>{{ userData.fullname }}</h4>
            <h4>{{ userData.phone }}</h4>
          </div>
        </div>
        <hr />
        <div class="exp aligned">
          <h3>الشرح</h3>
          <hr />
          <br />
          <h5>{{ lesson.explain }}</h5>
        </div>
        <!------DOWNLOAD------->
        <div class="row justify-content-center p-2">
          <div class="col-md-4">
            <button
              type="submit"
              :disabled="!lesson.filePath.includes('http')"
              @click="() => downloadFile(lesson._id)"
              class="btn btn-secondary mx-auto"
            >
              تحميل ملف الشرح
            </button>
            <a target="_blank" style="display: none" ref="file" download>download</a>
            <!-- <button ion-button v-if="mobile && pdf" :href="pdf">Download PDF</button> -->
          </div>
        </div>
      </div>
      <hr />
      <br />
      <!----------------HOMEWORK---------------------------->
      <div class="row justify-content-center p-2">
        <div class="col-md-4">
          <button
            :disabled="!userTask"
            @click="
              () => {
                $router
                  .push('/videos/' + $route.params.unit + '/answerTaskUser/' + lesson._id)
                  .catch(() => {});
              }
            "
            class="btn btn-primary"
          >
            {{ userTaskSolution ? "أعادة حل الواجب" : "قم بحل الواجب" }}
          </button>
        </div>
        <div class="col-md-4">
          <button
            :disabled="!userTaskSolution"
            @click="
              () => {
                $router
                  .push(
                    '/videos/' +
                      $route.params.unit +
                      '/userTaskSolution/' +
                      $store.getters.userId +
                      '/' +
                      lesson._id
                  )
                  .catch(() => {});
              }
            "
            class="btn btn-success"
          >
            تصحيح الواجب
          </button>
        </div>
      </div>
      <hr />
      <br />
    </div>
  </div>
</template>

<script>
import VideoIframe from "./video_iframe.vue";
import VideoStream from "./video_stream.vue";
import YoutubeStream from "./youtube-stream.vue";
import { Capacitor, FilesystemDirectory, Plugins } from "@capacitor/core";
import axios from "axios";
const { Filesystem } = Plugins;
import { url } from "../../main";

export default {
  props: ["lesson", "userData"],
  components: {
    VideoIframe,
    VideoStream,
    YoutubeStream,
  },
  created() {
    this.mobile = Capacitor.isNative;
    this.getUserTaskAndSolution();
  },
  data() {
    return {
      pdf: null,
      mobile: false,
      loading: false,
      videoStarted: false,
      userTask: null,
      userTaskSolution: null,
      vidPassword: null,
    };
  },
  computed: {
    videoType() {
      const video = this.lesson.videoPath;
      if (!video.includes("http")) return "storage-stream";
      else if (video.includes("embed") && video.includes("youtube"))
        return "youtube-embed";
      else if (!video.includes("embed") && video.includes("youtube"))
        return "youtube-stream";
    },
    watermark() {
      return this.$store.getters.adminData.watermark;
    },
  },
  watch: {
    lesson() {},
    videoStarted(val) {
      this.$emit("videoStarted", val);
    },
  },
  methods: {
    async downloadFile(id) {
      const fileUrl = url() + "/downloadFile/" + id;
      if (!Capacitor.isNative || Capacitor.isNative) {
        window.open(fileUrl, "_blank");
      } else {
      }
    },
    getUserTaskAndSolution() {
      this.userTaskSolution = null;
      this.userTask = null;
      axios
        .post("/getUserTaskAndSolution", {
          userId: localStorage.getItem("userId"),
          lessonId: this.lesson._id,
        })
        .then((res) => {
          this.userTask = res.data.task;
          this.userTaskSolution = res.data.solution;
        });
    },
    startVideo() {
      if (this.videoType == "youtube-stream") {
        axios.get("/authorizeVideo/" + this.$route.params.unit).then((res) => {
          if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
          this.vidPassword = res.data.password;
          this.videoStarted = true;
        });
      } else {
        this.videoStarted = true;
      }
    },
  },
};
</script>

