<template>
  <div id="lessons">
    <div
      class="overlay-image"
      :style="{
        background:
          'url(\'' +
          adminData.image1.replace('http', 'https') +
          '\') fixed center center no-repeat',
        backgroundSize: '100% 100%',
      }"
    ></div>
    <div class="content-fixed" style="background: #191b1e99">
      <div v-if="!videoStarted" @click="toggle" class="toggle">
        <i class="fa fa-list"></i>
      </div>

      <!-- Page Content -->

      <div class="videos-wrapper">
        <div class="text-center heading">
          <h1 class="">منصة {{ adminData.siteName }} <span></span></h1>
          <hr />
          <h4 class="">لطلبة الثانوية العامة</h4>
        </div>
        <hr />
        <div
          class="choose"
          v-if="
            !lesson &&
            !exam &&
            !solution &&
            !$route.params.lessonId &&
            !$route.params.userId
          "
        >
          <h2>اختار درس أو أمتحان...</h2>
        </div>
        <!-- ------------------------------------------------ -->
        <!-- ------------------------------------------------ -->
        <lesson
          v-if="lesson"
          :lesson="lesson"
          :userData="userData"
          @videoStarted="(val) => (videoStarted = val)"
        />
        <div class="p-md-2 p-0">
          <loading v-if="examLoading" />
          <!-- ------------------------------------------------ -->
          <exam
            v-if="exam && solutionModel"
            :exam="exam"
            :isExam="true"
            :solution_model="solutionModel"
          />
          <!-- ------------------------------------------------ -->
          <exam v-if="solution" :isResult="true" :exam="solution" />
          <!-- ------------------------------------------------ -->
          <router-view />
        </div>
        <!-- ------------------------------------------------ -->
        <!-- ------------------------------------------------ -->
      </div>

      <div class="videos-sidebar" :class="{ open: open }">
        <div class="sidebar-overlay"></div>
        <nav style="padding-top: 70px">
          <div class="sidebar-header">
            <h3 class="">قائمة الفيديوهات</h3>
          </div>
          <div class="center">
            <loading v-if="loading1" />
          </div>
          <ul v-if="!loading1" class="mb-5">
            <li
              class="mt-2"
              :class="{
                aligned: true,
                locked: lesson.locked,
                active: i === number,
              }"
              @click="() => openLesson(lessons[i], i)"
              v-for="(lesson, i) in lessons"
              :key="i"
              dir="rtl"
            >
              <div class="right">
                <span>({{ lesson.number }}) </span>
                <span>({{ lesson.name }})</span>
              </div>
              <div class="left">
                <img
                  width="30px"
                  height="30px"
                  src="../../assets/img/lock.png"
                  alt=""
                  class="img-fluid"
                />
              </div>
            </li>
          </ul>
          <!---------------------------------->
          <div class="sidebar-header exams-header">
            <h3 class="">قائمة الأمتحانات</h3>
          </div>
          <div class="center">
            <loading v-if="loading2" />
          </div>
          <ul v-if="!loading2" class="mb-5 exams-ul">
            <li
              v-for="(exam, i) in exams"
              :key="i"
              class="mt-2"
              :class="{ aligned: true, active: i === exam_number }"
              dir="rtl"
            >
              <div @click="startExam(exam._id, i)">
                <h5>أمتحان رقم ({{ exam.number }})</h5>
                <h5>{{ exam.name }}</h5>
              </div>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import VideoIframe from "./video_iframe.vue";
import VideoStream from "./video_stream.vue";
import Lesson from "./lesson.vue";
import Exam from "../exam-component.vue";
import Solution from "../result.vue";
import { url } from "../../main";
import { generateExamModel } from "../../functions";

export default {
  components: {
    VideoIframe,
    VideoStream,
    Lesson,
    Exam,
    Solution,
  },
  mounted() {
    // document.oncontextmenu = () => this.$router.push("/");
    // document.onmouseleave = () => this.$router.push("/");
  },
  created() {
    this.unit = +this.$route.params.unit;
    const stage = this.$route.params.stage;
    this.loading1 = true;
    if (stage) {
      axios.get("/fetchVideosForVisitors/" + stage + "/" + this.unit).then((res) => {
        this.loading1 = false;
        this.lessons = res.data.videos;
      });
      return;
    }
    this.getLessonsForStudent();
    this.getExams();
  },
  data() {
    return {
      lessons: [],
      exams: [],
      number: null,
      exam_number: null,
      userData: {},
      open: true,
      loading1: false,
      loading2: false,
      videoStarted: false,
      unit: null,
      //////////////////
      lesson: null,
      exam: null,
      solutionModel: null,
      solution: null,
      examLoading: false,
    };
  },
  computed: {
    adminData() {
      return this.$store.getters.adminData;
    },
  },
  watch: {
    $route(to, from) {
      if (this.$route.params.lessonId || this.$route.params.userId) {
        this.lesson = null;
      } else {
        this.number = null;
        this.exam_number = null;
      }
    },
  },
  methods: {
    async startLesson(lesson, i) {
      this.$router.push("/videos/" + this.$route.params.unit);
      this.number = i;
      this.exam_number = -1;
      this.lesson = null;
      this.solution = null;
      setTimeout(() => {
        this.lesson = lesson;
        this.exam = null;
        this.open = false;
      }, 200);
    },
    startExam(id, i) {
      this.$router.push("/videos/" + this.$route.params.unit);
      this.lesson = null;
      this.exam = null;
      this.solutionModel = null;
      this.solution = null;
      this.open = false;
      this.exam_number = i;
      this.number = -1;
      this.examLoading = true;
      axios
        .get(`/fetchUnitExamForUser/${id}/${this.$store.getters.userId}/${this.unit}`)
        .then((res) => {
          this.examLoading = false;
          if (res.data.err) {
            return this.$store.dispatch("writemessage", res.data.err);
          }
          if (res.data.isExam) {
            this.exam = res.data.exam;
            this.solutionModel = {
              sections: generateExamModel(this.exam),
            };
          } else if (res.data.isSolution) {
            this.solution = res.data.solution;
          }
        });
    },
    toggle() {
      this.open = !this.open;
    },
    getLessonsForStudent() {
      axios.post("/getUserData", { _id: localStorage.getItem("userId") }).then((res) => {
        this.userData = res.data.userdata;
      });
      axios
        .get(
          "/fetchVideosForStudent/" +
            localStorage.getItem("userId") +
            "/" +
            localStorage.getItem("stage") +
            "/" +
            this.unit
        )
        .then((res) => {
          this.loading1 = false;
          if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
          this.lessons = res.data.videos;
        });
    },
    getExams() {
      const unit = this.$route.params.unit;
      const stage = this.$store.getters.stage;
      this.loading2 = true;
      axios.get(`/fetchUnitExamsForUser/${stage}/${unit}`).then((res) => {
        this.loading2 = false;
        this.exams = res.data.exams;
      });
    },
    async openLesson(lesson, i) {
      if (lesson.locked) return;
      if (!lesson.coded) this.startLesson(lesson, i);
      else {
        const code = prompt("قم بأدخال الكود الخاص بالدرس", "******");
        if (code != "") {
          const res = await axios.get(
            `/getLessonByCode/${lesson._id}/${this.$store.getters.userId}/${code}`
          );
          if (res.data.err) {
            if (res.data.err == "code") return alert("الكود غير صحيح");
            else return res.data.err;
          }
          lesson = res.data.lesson;
          this.startLesson(lesson, i);
        }
      }
    },
  },
};
</script>

